<template>
  <div class="box">
    <!--  -->
    <my-bread class="bread" level1="" level2=""></my-bread>

    <el-row>
      <el-col>
        <el-button class="addbtn" type="primary" @click="showAddCateDialog"
          ></el-button
        >
      </el-col>
    </el-row>
    <!--  -->
    <div class="table">
      <tree-table
        class="treeTable"
        :data="cateList"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        index-text="#"
        :show-row-hover="false"
        name="table"
        show-index
        border
      >
        <!--  -->
        <template slot="isOk" slot-scope="scope">
          <i
            class="el-icon-success"
            style="color: lightgreen"
            v-if="scope.row.cat_deleted === false"
          ></i>
        </template>
        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag
            size="mini"
            type="success"
            v-else-if="scope.row.cat_level === 1"
            ></el-tag
          >
          <el-tag size="mini" type="warning" v-else></el-tag>
        </template>
        <!--  -->
        <template slot="opt" slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="showEditCateDialog(scope.row.cat_id)"
            circle
            plain
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="removeCate(scope.row.cat_id)"
            circle
            plain
          ></el-button>
        </template>
      </tree-table>
    </div>

    <!--  -->
    <el-pagination
      class="pagination"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[3, 5, 10, 15]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addCateDialogVisible"
      width="50%"
      @close="addCateDialogClosed"
    >
      <el-form
        :model="addCateForm"
        :rules="addCateFormRules"
        ref="addCateFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <!-- options -->
          <!-- props -->
          <el-cascader
            v-model="selectedKeys"
            :options="parentCateList"
            :props="cascaderProps"
            @change="parentCateChanged"
            clearable
            filterable
            style="width: 100%"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editCateDialogVisible"
      width="50%"
    >
      <el-form
        :model="editCateForm"
        :rules="editCateFormRules"
        ref="editCateFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="editCateForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="eidtCate"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      cateList: [],
      // 
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5,
      },
      total: 0,
      // table
      columns: [
        {
          label: "",
          prop: "cat_name",
        },
        {
          label: "",
          //  
          type: "template",
          template: "isOk",
        },
        {
          label: "",
          //  
          type: "template",
          template: "order",
        },
        {
          label: "",
          //  
          type: "template",
          template: "opt",
        },
      ],
      // 
      addCateDialogVisible: false,
      // 
      addCateForm: {
        // 
        cat_name: "",
        // id
        cat_pid: 0,
        // `0``1``2`
        cat_level: 0,
      },
      // 
      addCateFormRules: {
        cat_name: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      // 
      parentCateList: [],
      // 
      cascaderProps: {
        //  hover/click
        expandTrigger: "hover",
        value: "cat_id",
        label: "cat_name",
        children: "children",
      },
      // Id
      selectedKeys: [],
      //  
      editCateDialogVisible: false,
      // 
      editCateFormRules: {
        cat_name: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      //  
      editCateForm: "",
    };
  },
  created() {
    this.getCateList();
  },
  methods: {
    // 
    getCateList() {
      this.$http
        .get("categories", {
          params: this.queryInfo,
        })
        .then((res) => {
          console.log(res);
          if (res.data.meta.status !== 200) {
            return this.$message.error("");
          }
          // 
          this.cateList = res.data.data.result;
          console.log(this.cateList);
          // 
          this.total = res.data.data.total;
        });
    },
    //  pageSizeChange
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getCateList();
    },
    //  pagenum
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getCateList();
    },
    // 
    showAddCateDialog() {
      // 
      this.getParentCateList();
      // 
      this.addCateDialogVisible = true;
    },
    // 
    async getParentCateList() {
      const { data: res } = await this.$http.get("categories", {
        params: { type: 2 },
      });
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.parentCateList = res.data;
    },
    //  
    parentCateChanged() {
      // selectKeys >0 
      if (this.selectedKeys.length > 0) {
        // Id
        this.addCateForm.cat_pid =
          this.selectedKeys[this.selectedKeys.length - 1];
        // 
        this.addCateForm.cat_level = this.selectedKeys.length;
        return 0;
      } else {
        // Id
        this.addCateForm.cat_pid = 0;
        // 
        this.addCateForm.cat_level = 0;
      }
    },
    // 
    addCate() {
      this.$refs.addCateFormRef.validate(async (valid) => {
        if (!valid) return;
        const { data: res } = await this.$http.post(
          "categories",
          this.addCateForm
        );
        if (res.meta.status !== 201) {
          return this.$message.error("");
        }

        this.$message.success("");
        this.getCateList();
        this.addCateDialogVisible = false;
      });
    },
    //  
    addCateDialogClosed() {
      this.$refs.addCateFormRef.resetFields();
      this.selectedKeys = [];
      this.addCateForm.cat_level = 0;
      this.addCateForm.cat_pid = 0;
    },
    // 
    async removeCate(id) {
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      const { data: res } = await this.$http.delete("categories/" + id);
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.$message.success("");
      this.getCateList();
    },
    // 
    async showEditCateDialog(id) {
      const { data: res } = await this.$http.get("categories/" + id);
      if (res.meta.status !== 200) return this.$message.error("");
      this.editCateForm = res.data;
      this.editCateDialogVisible = true;
    },
    // 
    eidtCate() {
      this.$refs.editCateFormRef.validate(async (valid) => {
        if (!valid) return;
        const { data: res } = await this.$http.put(
          "categories/" + this.editCateForm.cat_id,
          {
            cat_name: this.editCateForm.cat_name,
          }
        );
        if (res.meta.status !== 200)
          return this.$message.error("");
        this.$message.success("");
        this.getCateList();
        this.editCateDialogVisible = false;
      });
    },
  },
};
</script>

<style scoped>
.box {
  height: 100%;
}
.addbtn {
  margin-top: 15px;
}
.pagination {
  margin-top: 15px;
}
.table {
  max-height: 460px;
  overflow: auto;
}
.treeTable {
  margin-top: 15px;
}
.el-cascader {
  width: 100%;
}
</style>



