<template>
  <div class="box">
    <!--  -->
    <!--  -->
    <my-bread class="bread" level1="" level2=""></my-bread>
    <!--  -->
    <el-row class="searchrow">
      <el-col>
        <el-input
          @clear="loadUserList"
          clearable
          placeholder=""
          v-model="query"
          class="inputSearch"
        >
          <el-button
            @click="queryUser()"
            slot="append"
            icon="el-icon-search"
          ></el-button>
        </el-input>
        <el-button type="success" @click="showAddUser" plain
          ></el-button
        >
      </el-col>
    </el-row>
    <!--  -->
    <el-table
      class="table"
      :data="userList"
      max-height="460"
      style="width: 100%"
    >
      <el-table-column type="index" label="#" width="100"> </el-table-column>
      <el-table-column prop="username" label="" width="180">
      </el-table-column>
      <el-table-column prop="email" label=""> </el-table-column>
      <el-table-column prop="mobile" label=""> </el-table-column>
      <el-table-column label="">
        <!-- slot-scope -->
        <template slot-scope="scope">
          {{ scope.row.create_time | fmtdate }}
        </template>
      </el-table-column>
      <el-table-column label="" width="150">
        <template slot-scope="scope">
          <el-switch
            @change="changeMgstate(scope.row)"
            v-model="scope.row.mg_state"
            active-color="#13ce66"
            inactive-color="rgba(0,0,0,0.2)"
          >
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column prop="address" label="">
        <template slot-scope="scope">
          <!-- size="mini"  plain circle-->
          <el-button
            size="mini"
            type="primary"
            icon="el-icon-edit"
            @click="showUpdateUser(scope.row)"
            plain
            circle
          ></el-button>
          <el-button
            size="mini"
            type="danger"
            icon="el-icon-delete"
            @click="deleteUser(scope.row.id)"
            plain
            circle
          ></el-button>
          <el-button
            size="mini"
            type="success"
            icon="el-icon-check"
            @click="showSetUserRole(scope.row)"
            plain
            circle
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <!-- 
     size-change 
     current-change 
     current-page
     page-sizes
     page-size
     layout
     total
     -->
    <el-pagination
      class="pagination"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pagenum"
      :page-sizes="[3, 6, 9]"
      :page-size="3"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisibleAdd">
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            style="width: 90%"
            v-model="form.username"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label=" " :label-width="formLabelWidth">
          <el-input
            style="width: 90%"
            v-model="form.password"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label=" " :label-width="formLabelWidth">
          <el-input
            style="width: 90%"
            v-model="form.email"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label=" " :label-width="formLabelWidth">
          <el-input
            style="width: 90%"
            v-model="form.mobile"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleAdd = false"> </el-button>
        <el-button type="success" @click="addUser"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisibleUpdate">
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            style="width: 90%"
            disabled
            v-model="form.username"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label=" " :label-width="formLabelWidth">
          <el-input
            style="width: 90%"
            v-model="form.email"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label=" " :label-width="formLabelWidth">
          <el-input
            style="width: 90%"
            v-model="form.mobile"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="Cancel"> </el-button>
        <el-button type="success" @click="updateUser"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisibleRole">
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          {{ currUsername }}
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          {{ currRoleId }}
          <el-select v-model="currRoleId" placeholder="">
            <el-option disabled label="" :value="-1"></el-option>
            <el-option
              :label="item.roleName"
              v-for="(item, i) in roles"
              :key="i"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleRole = false"> </el-button>
        <el-button type="primary" @click="setUserRole"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //
      query: "",
      userList: [],
      //
      total: -1,
      pagenum: 1,
      pagesize: 3,
      //
      dialogFormVisibleAdd: false,
      dialogFormVisibleDel: false,
      dialogFormVisibleUpdate: false,
      dialogFormVisibleRole: false,

      formLabelWidth: "100px",
      //ID
      currRoleId: -1,
      currUserId: -1,
      currUsername: "",
      roles: [],
      //
      form: {
        username: "",
        password: "",
        eamil: "",
        mobile: "",
      },
    };
  },
  created() {
    //
    this.getUserList();
  },
  methods: {
    Cancel() {
      this.dialogFormVisibleUpdate = false;
      this.getUserList();
    },
    //
    showSetUserRole(user) {
      this.dialogFormVisibleRole = true;
      this.currUsername = user.username;
      this.currUserId = user.id;
      this.$http.get(`users/${this.currUserId}`).then((res) => {
        this.currRoleId = res.data.data.rid;
      });
      this.$http.get(`roles`).then((res) => {
        this.roles = res.data.data;
      });
    },
    //
    setUserRole() {
      this.dialogFormVisibleRole = false;
      console.log(this.currRoleId);
      this.$http
        .put(`users/${this.currUserId}/role`, { rid: this.currRoleId })
        .then((res) => {
          this.currRoleId = res.data.data.rid;
        });
    },
    //
    changeMgstate(user) {
      this.$http
        .put(`users/${user.id}/state/${user.mg_state}`)
        .then((res) => {});
    },
    //
    showUpdateUser(user) {
      this.dialogFormVisibleUpdate = true;
      this.form = user;
    },
    //
    updateUser() {
      this.dialogFormVisibleUpdate = false;
      this.$http.put(`users/${this.form.id}`, this.form).then((res) => {
        const {
          meta: { status, msg },
          data,
        } = res.data;
        if (status === 200) {
          this.pagenum = 1;
          this.getUserList();
          this.$message({
            type: "success",
            message: msg,
          });
        } else {
          this.getUserList();
          this.$message({
            type: "warning",
            message: msg,
          });
        }
      });
    },
    //
    deleteUser(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.$http.delete(`users/${id}`).then((res) => {
            const {
              meta: { msg, status },
            } = res.data;
            if (status === 200) {
              this.pagenum = 1;
              this.getUserList();
              this.$message({
                type: "success",
                message: msg,
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    showAddUser() {
      this.form = {};
      this.dialogFormVisibleAdd = true;
    },
    //
    addUser() {
      this.dialogFormVisibleAdd = false;
      this.$http.post(`users`, this.form).then((res) => {
        const {
          meta: { status, msg },
          data,
        } = res.data;
        if (status === 201) {
          this.$message.success(msg);
          this.getUserList();
          //form
          this.form = {};
          //or
          // for(const key in this.form){
          //   this.form[key]=''
          // }
        } else {
          this.$message.warning(msg);
        }
      });
    },
    //
    loadUserList() {
      this.getUserList();
    },
    //
    queryUser() {
      this.getUserList();
    },
    //
    handleSizeChange(val) {
      this.pagesize = val;
      this.pagenum = 1;
      this.getUserList();
    },
    handleCurrentChange(val) {
      this.pagenum = val;
      this.getUserList();
    },
    //
    getUserList() {
      this.$http
        .get(
          `users?query=${this.query}&pagenum=${this.pagenum}&pagesize=${this.pagesize}`
        )
        .then((res) => {
          const {
            meta: { status, msg },
            data: { users, total },
          } = res.data;
          if (status === 200) {
            //
            this.userList = users;
            //total
            this.total = total;
            //
            // this.$message.success(msg);
          } else {
            //
            this.$message.warning(msg);
          }
        });
    },
  },
};
</script>
<style>
.box {
  height: 100%;
}
.inputSearch {
  width: 300px;
}
.searchrow {
  margin-top: 20px;
}
.table {
  margin-top: 10px;
}
.pagination {
  margin-top: 15px;
}
</style>


