<template>
  <div class="box">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-row class="row">
      <el-col :span="6">
        <el-input placeholder="">
          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
      </el-col>
    </el-row>

    <!--  -->
    <el-table class="table" max-height="460" :data="orderList">
      <el-table-column type="index" label="#"></el-table-column>
      <el-table-column label="" prop="order_number"></el-table-column>
      <el-table-column label="" prop="order_price"></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-tag type="danger" size="mini" v-if="scope.row.pay_status"
            ></el-tag
          >
          <el-tag type="success" size="mini" v-else></el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" prop="is_send"></el-table-column>
      <el-table-column label="" prop="create_time"></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="mini"
            icon="el-icon-edit"
            @click="showEditDialog"
            circle
            plain
          ></el-button>
          <el-button
            type="success"
            size="mini"
            icon="el-icon-location"
            @click="showProgressDialog(scope.row.id)"
            circle
            plain
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination class="pagination"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    ></el-pagination>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addressDialogVisible"
      width="50%"
      @close="addressDialogClosed"
    >
      <el-form
        :model="addressForm"
        :rules="addressFormRules"
        ref="addressFormRef"
        label-width="100px"
      >
        <el-form-item label="/" prop="address1">
          <el-cascader
            v-model="addressForm.address1"
            :options="cityData"
            :props="{ expandTrigger: 'hover' }"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = false"
          > </el-button
        >
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="progressDialogVisible"
      width="50%"
    >
      <!--  -->
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in progressInfo"
          :key="index"
          :timestamp="activity.time"
          >{{ activity.context }}</el-timeline-item
        >
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityData from "./citydata.js";

export default {
  data() {
    return {
      // 
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 10,
      },
      total: 0,
      // 
      orderList: [],
      // 
      addressDialogVisible: false,
      addressForm: {
        address1: [],
        address2: "",
      },
      addressFormRules: {
        address1: [
          { required: true, message: "", trigger: "blur" },
        ],
        address2: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      cityData,
      // 
      progressDialogVisible: false,
      // 
      progressInfo: [],
    };
  },
  created() {
    this.getOrderList();
  },
  methods: {
    async getOrderList() {
      const { data: res } = await this.$http.get("orders", {
        params: this.queryInfo,
      });
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.total = res.data.total;
      this.orderList = res.data.goods;
    },
    // 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getOrderList();
    },
    handleCurrentChange(newSize) {
      this.queryInfo.pagenum = newSize;
      this.getOrderList();
    },
    showEditDialog() {
      this.addressDialogVisible = true;
    },
    addressDialogClosed() {
      this.$refs.addressFormRef.resetFields();
    },
    async showProgressDialog(id) {
      // 1106975712662
      const { data: res } = await this.$http.get(`/kuaidi/${id}`);
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error("!");
      }
      this.progressInfo = res.data;
      this.progressDialogVisible = true;
    },
  },
};
</script>

<style  scoped>
.box {
  height: 100%;
}
.el-cascader {
  width: 100%;
}
.row{
  margin-top: 15px;
}
.table{
  margin-top: 15px;
}
.pagination{
  margin-top: 15px;
}
</style>



