<template>
  <div class="box">
    <!--  -->
    <!--  -->
    <!-- <my-bread></my-bread> -->
    <my-bread level1="" level2=""></my-bread>
    <!--  -->
    <el-table
      class="table"
      stripe
      :data="rightList"
      max-height="550"
      style="width: 100%"
    >
      <el-table-column type="index" label="#" width="100"> </el-table-column>
      <el-table-column
        prop="authName"
        label=""
        :width="columnwidth"
      ></el-table-column>
      <el-table-column prop="path" label="" :width="columnwidth">
      </el-table-column>
      <el-table-column prop="level" label="" >
        <template slot-scope="scope">
          <span v-if="scope.row.level==='0'"> </span>
          <span v-if="scope.row.level==='1'"> </span>
          <span v-if="scope.row.level==='2'"> </span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rightList: [],
      columnwidth: 200,
    };
  },
  created() {
    this.getRightList();
  },
  methods: {
    getRightList() {
      this.$http.get(`rights/list`).then((res) => {
        this.rightList = res.data.data;
      });
    },
  },
};
</script>

<style>
.box {
  height: 100%;
}
.table {
  margin-top: 10px;
}
</style>


