<template>
  <div class="box">
    <!--  -->
    <my-bread class="bread" level1="" level2=""></my-bread>
    <!--  -->
    <el-row class="searchrow">
      <el-col>
        <el-input
          @clear="loadGoodsList"
          clearable
          placeholder=""
          v-model="query"
          class="inputSearch"
        >
          <el-button
            @click="queryGoods()"
            slot="append"
            icon="el-icon-search"
          ></el-button>
        </el-input>
        <el-button
          type="success"
          @click="$router.push({ name: `goodsadd` })"
          plain
          ></el-button
        >
      </el-col>
    </el-row>
    <!--  -->
    <el-table
      class="table"
      :data="goodsList"
      max-height="460"
      style="width: 100%"
    >
      <el-table-column type="index" label="#" width="100"> </el-table-column>
      <el-table-column prop="goods_name" label=""> </el-table-column>
      <el-table-column prop="goods_price" label=""> </el-table-column>
      <el-table-column prop="goods_number" label=""> </el-table-column>
      <el-table-column label="">
        <!-- slot-scope -->
        <template slot-scope="scope">
          {{ scope.row.add_time | fmtdate }}
        </template>
      </el-table-column>
      <el-table-column prop="address" label="">
        <template slot-scope="scope">
          <!-- size="mini"  plain circle-->
          <el-button
            size="mini"
            type="primary"
            icon="el-icon-edit"
            @click="showUpdateGoods(scope.row)"
            plain
            circle
          ></el-button>
          <el-button
            size="mini"
            type="danger"
            icon="el-icon-delete"
            @click="deleteGoods(scope.row.id)"
            plain
            circle
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      class="pagination"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pagenum"
      :page-sizes="[3, 6, 9]"
      :page-size="3"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      query: "",
      goodsList: [],
      //
      total: -1,
      pagenum: 1,
      pagesize: 3,
    };
  },
  created() {
    this.getGoodsList();
  },
  methods: {
    handleSizeChange(val) {
      this.pagesize = val;
      this.pagenum = 1;
      this.getGoodsList();
    },
    handleCurrentChange(val) {
      this.pagenum = val;
      this.getGoodsList();
    },
    loadGoodsList() {
      this.getGoodsList();
    },
    queryGoods() {
      this.getGoodsList();
    },
    // 
    getGoodsList() {
      this.$http
        .get(
          `goods?query=${this.query}&pagenum=${this.pagenum}&pagesize=${this.pagesize}`
        )
        .then((res) => {
          console.log(res);
          const {
            meta: { status, msg },
            data: { goods, total },
          } = res.data;
          if (status === 200) {
            //
            this.goodsList = goods;
            //total
            this.total = total;
            //
          } else {
            //
            this.$message.warning(msg);
          }
        });
    },
  },
};
</script>

<style>
.box {
  height: 100%;
}
.inputSearch {
  width: 350px;
}
.searchrow {
  margin-top: 20px;
}
.table {
  margin-top: 10px;
}
.pagination {
  margin-top: 15px;
}
</style>


